import { compileVerbose } from 'func-compiler';
import { isSuccess } from 'func-compiler/dist/util';
import { Context } from 'func-compiler/dist/semantics/types/context';
import { Scheme } from 'func-compiler/dist/semantics/types/scheme';
import { BuiltinName } from 'func-compiler/dist/builtins';
import { separateBuiltins } from 'func-compiler/dist/codeGeneration';
import { validJsName } from 'func-compiler/dist/codeGeneration/transpile/names';

export enum State {
    Loading,
    Success,
    Error,
}

export interface SymbolTableEntry {
    value: unknown;
    scheme: Scheme;
}

export interface SymbolTable {
    [key: string]: SymbolTableEntry;
}

interface LoadingStatus {
    state: State.Loading;
}

export interface SuccessStatus {
    state: State.Success;
    // fullSymbolTable: SymbolTable;
    userSymbolTable: SymbolTable;
    userCode: string;
    fullCode: string;
}

interface ErrorStatus {
    state: State.Error;
    message: string;
}

export type CompilationStatus = LoadingStatus
    | SuccessStatus
    | ErrorStatus;

export const LOADING_STATUS: LoadingStatus = {
    state: State.Loading,
};

export function compile(source: string): SuccessStatus | ErrorStatus {
    const result = compileVerbose(source);
    if (isSuccess(result)) {
        const { types, code } = result.value;
        const userSymbolTable = constructSymbolTable(types, code);
        return {
            state: State.Success,
            userSymbolTable,
            // fullSymbolTable,
            userCode: separateBuiltins(code)[1],
            fullCode: code,
        };
    } else {
        return {
            state: State.Error,
            message: result.error,
        };
    }
}

function constructSymbolTable(typeTable: Context, code: string): SymbolTable {
    return Object.entries(typeTable)
        .filter(
            ([varName]) => !Object.values(BuiltinName).includes(varName as BuiltinName),
        )
        .reduce(
            (acc, [varName, varType]): SymbolTable => ({
                ...acc,
                [varName]: {
                    value: evaluateAndRead(code, varName),
                    scheme: varType,
                },
            }),
            {},
        );
}

function evaluateAndRead(source: string, variable: string) {
    const code = `${source}\nreturn ${validJsName(variable)}`;
    /* eslint-disable-next-line no-new-func */
    return new Function(code)();
}
