<template>
    <label
        for="select-sample"
        class="option-label"
    >
        Select sample to edit:
    </label>
    <select
        id="select-sample"
        v-model="selected"
        v-on:change="selectSample"
    >
        <option
            v-for="sampleName in sampleNames"
            v-bind:key="sampleName"
            v-bind:value="sampleName">
            {{ sampleName }}
        </option>
    </select>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

interface Data {
    selected: string;
    samples: {
        [name: string]: string;
    };
}

interface SampleDto {
    name: string;
    code: string;
}

async function loadSamples(): Promise<SampleDto[]> {
    const response = await fetch('./samples.json');
    return response.json();
}

const DEFAULT_SAMPLE = 'intro';

export default defineComponent({
    name: 'SelectSample',
    props: {
        setSource: {
            type: Function,
            required: true,
        },
    },
    data(): Data {
        return {
            selected: DEFAULT_SAMPLE,
            samples: {},
        };
    },
    async created() {
        const sampleArray = await loadSamples();
        this.samples = sampleArray.reduce(
            (acc, sample) => ({ ...acc, [sample.name]: sample.code }),
            {},
        );
        this.setSelectedSample(DEFAULT_SAMPLE);
    },
    computed: {
        sampleNames(): string[] {
            return Object.keys(this.samples);
        },
    },
    methods: {
        setSelectedSample(sampleName: string) {
            this.setSource(this.samples[sampleName]);
        },
        selectSample(event: { target: { value: string }}) {
            const selectedName = event.target.value;
            this.setSelectedSample(selectedName);
        },
    },
});

</script>

<style scoped lang="scss">
@import "./src/styles/variables.scss";

option, select {
    cursor: pointer
}

</style>
