<template>
    <div class="code-display">
        <pre class="code-text code-container" v-html="highlightedCode"></pre>
        <div class="options-container">
            <label for="builtin-toggle">
                {{ builtinToggleText }}
            </label>
            <input
                id="builtin-toggle"
                type="checkbox"
                v-model="showBuiltins"
                name="Show builtins"
            >
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';
import Prism from 'prismjs';

export default defineComponent({
    name: 'CodeDisplay',
    props: {
        fullCode: {
            type: String,
            required: true,
        },
        userCode: {
            type: String,
            required: true,
        },
    },
    data() {
        return {
            showBuiltins: false,
        };
    },
    computed: {
        highlightedCode(): string {
            return Prism.highlight(this.code, Prism.languages.javascript, 'javascript');
        },
        code(): string {
            return this.showBuiltins ? this.fullCode : this.userCode;
        },
        builtinToggleText(): string {
            return this.showBuiltins ? 'Hide builtins' : 'Show builtins';
        },
    },
});
</script>

