
import { defineComponent } from 'vue';
import Prism from 'prismjs';

export default defineComponent({
    name: 'CodeDisplay',
    props: {
        fullCode: {
            type: String,
            required: true,
        },
        userCode: {
            type: String,
            required: true,
        },
    },
    data() {
        return {
            showBuiltins: false,
        };
    },
    computed: {
        highlightedCode(): string {
            return Prism.highlight(this.code, Prism.languages.javascript, 'javascript');
        },
        code(): string {
            return this.showBuiltins ? this.fullCode : this.userCode;
        },
        builtinToggleText(): string {
            return this.showBuiltins ? 'Hide builtins' : 'Show builtins';
        },
    },
});
