<template>
    <div class="reading-container">
        <pre
            v-for="([isComment, content], index) in sections"
            v-bind:key="index"
            class="code-text"
            v-bind:class="{ 'non-comment': !isComment }"
        >{{ content }}</pre>
    </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';

function groupBy<T, V>(array: T[], predicate: (x: T) => V): [V, T[]][] {
    if (!array.length) {
        return [];
    }

    let groupingValue: V = predicate(array[0]);
    let currentGroup: T[] = [];
    const result: [V, T[]][] = [];

    array.forEach(element => {
        const predicateValue = predicate(element);
        if (predicateValue === groupingValue) {
            currentGroup.push(element);
        } else {
            result.push([groupingValue, currentGroup]);
            groupingValue = predicateValue;
            currentGroup = [element];
        }
    });

    if (currentGroup.length) {
        result.push([groupingValue, currentGroup]);
    }

    return result;
}

const COMMENT_MARKER = '//';

function removeCommentMarker(comment: string): string {
    const result = comment.substring(COMMENT_MARKER.length).trimStart();
    return result;
}

export default defineComponent({
    name: 'CodePreview',
    props: {
        content: {
            type: String,
            required: true,
        },
    },
    computed: {
        lines(): string[] {
            return this.content.split('\n');
        },
        sections(): [boolean, string][] {
            return groupBy(this.lines, line => line.startsWith(COMMENT_MARKER))
                .map(([isComment, sectionLines]) => [
                    isComment,
                    isComment ? sectionLines.map(comment => removeCommentMarker(comment)) : sectionLines,
                ])
                .map(([isComment, sectionLines]) => [
                    isComment,
                    (sectionLines as string[]).join('\n')],
                )
                .map(
                    ([isComment, section]) => [
                        isComment,
                        (section as string).trim(),
                    ],
                )
                .filter(
                    ([isComment, section]) => (section as string).length !== 0,
                ) as [boolean, string][];
        },
    },
});
</script>

