<template>
    <div class="info">
        <div class="inspected">
            <span>{{ inspected }}</span>
        </div>
        <div class="symbol-table">
            <div class="table-header">
                <div class="table-cell">Name</div>
                <div class="table-cell">Inferred Type</div>
                <div class="table-cell">Value After Execution (JS)</div>
            </div>
            <div class="table-contents">
                <div
                    v-for="entry in tableEntries"
                    v-bind:key="entry.name"
                    class="table-row"
                >
                    <div
                        class="table-cell"
                        @mouseover="showDetails(entry.name)"
                        @mouseout="resetDetails"
                    >
                        {{ entry.name }}
                    </div>
                    <div
                        class="table-cell"
                        @mouseover="showDetails(entry.type)"
                        @mouseout="resetDetails"
                    >
                        {{ entry.type }}
                    </div>
                    <div
                        class="table-cell"
                        @mouseover="showDetails(entry.value)"
                        @mouseout="resetDetails"
                    >
                        {{ entry.value }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from 'vue';
import { SymbolTable } from '@/compilation';
import { prepareSymbolInfo, SymbolInfo } from '@/components/SymbolInfo';

const INSPECTED_DEFAULT = 'Hover over a truncated field in the table to get full info';

// We don't want to flash the default text on every mouseout, this is for debouncing
const RESET_TIMOUT = 100;

let resetInterval: number | undefined;

export default defineComponent({
    name: 'SymbolTable',
    data() {
        return {
            inspected: INSPECTED_DEFAULT,
        };
    },
    props: {
        table: {
            type: Object as PropType<SymbolTable>,
            required: true,
        },
    },
    computed: {
        tableEntries(): SymbolInfo[] {
            return Object.entries(this.table).map(
                ([name, entry]) => prepareSymbolInfo(name, entry),
            );
        },
    },
    methods: {
        showDetails(field: string) {
            clearTimeout(resetInterval);
            this.inspected = field;
        },
        resetDetails() {
            resetInterval = setTimeout(
                () => {
                    this.inspected = INSPECTED_DEFAULT;
                },
                RESET_TIMOUT,
            );
        },
    },
});
</script>

