import { SymbolTableEntry } from '@/compilation';
import { showScheme } from 'func-compiler/dist/semantics/types/scheme';

export type SymbolInfo = {
    name: string;
    type: string;
    value: string;
};

export function prepareSymbolInfo(name: string, entry: SymbolTableEntry): SymbolInfo {
    return {
        name,
        type: showScheme(entry.scheme),
        value: stringifyValue(entry.value),
    };
}

function stringifyValue(value: unknown): string {
    switch (typeof value) {
    case 'object':
        return String(Array.isArray(value) ? `[${value.map(stringifyValue).join(', ')}]` : value);
    case 'boolean':
    case 'number':
    case 'undefined':
    case 'symbol':
    case 'bigint':
        return String(value);
    case 'string':
        return `"${value}"`;
    case 'function':
        return 'λ';
    default:
        return 'Default';
    }
}

function assertUnreachable(x: never): never {
    throw new Error('should never happen');
}
