<template>
    <main
        id="main"
        v-on:keydown.ctrl.enter.capture.prevent="compile"
    >
        <section class="column">
            <div class="column-header">
                <div class="button-container">
                    <SelectSample
                        v-bind:setSource="setSource"
                    />
                    <button
                        v-on:click="attemptCompile"
                        class="actionable"
                    >
                        <span>
                            <FontAwesomeIcon
                                icon="cog"
                                class="icon"
                            />
                            Compile (Ctrl + ⏎)
                        </span>
                    </button>
                </div>
            </div>
            <keep-alive>
                <Editor
                    v-if="isEditable"
                    v-bind:initial-content="initialSource"
                    v-model="source"
                />
                <CodePreview
                    v-else
                    v-bind:content="source"
                />
            </keep-alive>
        </section>
        <section class="column">
            <div class="column-header">
                <div class="tab-container">
                    <label class="option-label">
                        Code:
                    </label>
                    <button
                        v-for="codeOption in codeOptions"
                        v-bind:key="codeOption.name"
                        v-on:click="selectedCodeOption = codeOption.name"
                        class="tab"
                        v-bind:class="{ active: codeOption.name === selectedCodeOption }"
                    >
                        <span>
                            <FontAwesomeIcon
                                v-bind:icon="codeOption.icon"
                                class="icon"
                            />
                            {{ codeOption.name }}
                        </span>
                    </button>
                </div>
                <div class="tab-container">
                    <label class="option-label">
                        View:
                    </label>
                    <button
                        v-for="displayOption in displayOptions"
                        v-bind:key="displayOption.name"
                        class="tab"
                        v-bind:class="{ active: displayOption.name === selectedDisplayOption }"
                        @click="selectedDisplayOption = displayOption.name"
                    >
                      <span>
                          <FontAwesomeIcon
                              v-bind:icon="displayOption.icon"
                              class="icon"
                          />
                          {{ displayOption.name }}
                      </span>
                    </button>
                </div>
            </div>
            <Loader
                v-if="isLoading"
            />
            <Error
                v-else-if="!hasCompiledSuccessfully"
                v-bind:message="compilationStatus.message"
            />
            <SymbolTable
                v-else-if="showSymbolTable"
                v-bind:table="symbolTable"
            />
            <CodeDisplay
                v-else-if="showGeneratedCode"
                v-bind:fullCode="compilationStatus.fullCode"
                v-bind:userCode="compilationStatus.userCode"
            />
        </section>
    </main>
</template>

<script lang="ts">
import { defineComponent } from 'vue';
import Editor from './components/Editor.vue';
import SymbolTable from './components/SymbolTable.vue';
import { CompilationStatus, compile, LOADING_STATUS, State, SuccessStatus, SymbolTable as Table } from '@/compilation';
import Error from '@/components/Error.vue';
import CodeDisplay from '@/components/CodeDisplay.vue';
import CodePreview from '@/components/CodePreview.vue';
import SelectSample from '@/components/SelectSample.vue';
import Loader from '@/components/Loader.vue';

enum CodeOptionName {
    Edit = 'Edit',
    Read = 'Read',
}

interface CodeOption {
    name: CodeOptionName;
    icon: string;
}

enum DisplayOptionName {
    SymbolTable = 'Symbol Table',
    GeneratedCode = 'Generated Code',
}

interface DisplayOption {
    name: DisplayOptionName;
    icon: string;
}

interface Data {
    source: string;
    initialSource: string;
    showBuiltins: boolean;
    compilationStatus: CompilationStatus;
    displayOptions: DisplayOption[];
    codeOptions: CodeOption[];
    selectedDisplayOption: DisplayOptionName;
    selectedCodeOption: CodeOptionName;
}

const STARTING_SOURCE = '';

const initialData: Data = {
    initialSource: STARTING_SOURCE,
    source: STARTING_SOURCE,
    showBuiltins: false,
    compilationStatus: compile(STARTING_SOURCE),
    displayOptions: [
        {
            name: DisplayOptionName.SymbolTable,
            icon: 'list',
        },
        {
            name: DisplayOptionName.GeneratedCode,
            icon: 'code',
        },
    ],
    selectedDisplayOption: DisplayOptionName.SymbolTable,
    codeOptions: [
        {
            name: CodeOptionName.Edit,
            icon: 'edit',
        },
        {
            name: CodeOptionName.Read,
            icon: 'book',
        },
    ],
    selectedCodeOption: CodeOptionName.Read,
};

export default defineComponent({
    name: 'App',
    created() {
        document.addEventListener('keydown', (event) => {
            if (event.ctrlKey && event.key === 'Enter') {
                this.compile();
            }
        });
    },
    data(): Data {
        return initialData;
    },
    computed: {
        hasCompiledSuccessfully(): boolean {
            return this.compilationStatus.state === State.Success;
        },
        isLoading(): boolean {
            return this.compilationStatus.state === State.Loading;
        },
        symbolTable(): Table {
            const { userSymbolTable } = this.compilationStatus as SuccessStatus;
            return userSymbolTable;
        },
        showGeneratedCode(): boolean {
            return this.selectedDisplayOption === DisplayOptionName.GeneratedCode;
        },
        showSymbolTable(): boolean {
            return this.selectedDisplayOption === DisplayOptionName.SymbolTable;
        },
        isEditable(): boolean {
            return this.selectedCodeOption === CodeOptionName.Edit;
        },
    },
    methods: {
        attemptCompile() {
            if (this.isLoading) {
                return;
            }
            this.compile();
        },
        compile() {
            this.compilationStatus = LOADING_STATUS;
            setTimeout(() => { this.compilationStatus = compile(this.source); }, 0);
        },
        setSource(source: string) {
            this.source = source;
            this.initialSource = source;
        },
    },
    watch: {
        initialSource() {
            this.compile();
        },
    },
    components: {
        Editor,
        SymbolTable,
        Error,
        CodeDisplay,
        Loader,
        SelectSample,
        CodePreview,
    },
});
</script>

